.ShowBooks {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.BooksView {
  margin-right: 30px;
  margin-bottom: 30px;
}

/* Text style */

.text-white {
  color: white;
}

.text-black {
  color: black;
}

img {
  border-radius: 10%;
  width: 250px;
  height: 350px;
}

.img-div {
  position: relative;
}
.top-left {
  position: absolute;
  top: 17px;
  left: 25px;
  color: white;
  background-color: grey;
  border-radius: 20px;
  padding: 10px;
  opacity: 0.75;
}

.bottom-right {
  position: absolute;
  bottom: 20px;
  right: 25px;
  color: white;
  background-color: rgb(19, 19, 19);
  padding: 10px;
  border-radius: 20px;
  opacity: 0.75;
}

/* CSS for the MODAL */
.modal {
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  animation-name: fadeIn;
  animation-duration: 0.5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal-content {
  border-radius: 20px;
  position: relative;
  margin: auto;
  padding: 20px;
  width: 500px;
  height: 650px;
  background-color: #fefefe;
}

.modal-content img {
  display: block;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
}

.close {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}
